.App {
  background-color:#000;
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #000000;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.body {
  color:#000;
}

.bg-dark {
  background-color:#000;
}

.bg-primary {
  background-color:#000;
}

/* All CSS related to Navigation bar components */

.navbar {
   background-color:#000;
}

.nav-link {
   font-family: "Chivo",sans-serif;
   font-size: 1rem;
   color:#fff;
}

.navbar-nav {
   font-family: "Overpass",sans-serif;
   font-size: 1rem;
   font-weight: 400;
   color:#fff;
}

.navbar-light .navbar-brand, .navbar-light .navbar-brand:focus, .navbar-light .navbar-brand:hover {
   font-family: "Overpass",sans-serif;
   font-size: 2rem;
   font-weight: 600;
   color: #fff;
}

.navbar-toggler {
   background-color:#fff;
}

